import React from 'react';
import './ModalLGPD.css';

function ModalLGPD({ onAceitar }) {
  return (
    <div className="modal" style={{ display: 'flex' }}>
      <div className="modal-content">
        <h2>Aceite os Termos de LGPD</h2>
        <p>
          Ao acessar este site, você consente com o tratamento dos seus dados pessoais de acordo com a Lei Geral de Proteção de Dados (LGPD).
        </p>
        <button onClick={onAceitar}>Aceitar</button>
      </div>
    </div>
  );
}

export default ModalLGPD;
