import React from "react";

const TabelaColaboradores = ({ colaboradores, onEditar, onExcluir, formatarSalario }) => {
  return (
    <div className="table-container">
      <table>
        <thead>
          <tr>
            <th>Nome</th>
            <th>Email</th>
            <th>Cargo</th>
            <th>Salário</th>
            <th>Ações</th>
          </tr>
        </thead>
        <tbody>
          {colaboradores.map((colaborador) => (
            <tr key={colaborador.id}>
              <td>{colaborador.nome}</td>
              <td>{colaborador.email}</td>
              <td>{colaborador.cargo}</td>
              <td>{formatarSalario(colaborador.salario)}</td> {/* Formatar salário */}
              <td>
                <button onClick={() => onEditar(colaborador)}>Editar</button>
                <button onClick={() => onExcluir(colaborador.id)}>Excluir</button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default TabelaColaboradores;
