import React, { useState, useEffect } from "react";
import * as XLSX from 'xlsx';
import TabelaColaboradores from "./components/TabelaColaboradores";
import ModalLGPD from './components/ModalLGPD';  // Importação do modal LGPD
import './App.css';

const App = () => {
  const [colaboradores, setColaboradores] = useState([]);
  const [isModalOpen, setIsModalOpen] = useState(false); // Estado para controle do modal LGPD
  const [isAddingColaborador, setIsAddingColaborador] = useState(false);
  const [nome, setNome] = useState('');
  const [email, setEmail] = useState('');
  const [cargo, setCargo] = useState('');
  const [salario, setSalario] = useState('');
  const [editingId, setEditingId] = useState(null); // Para identificar qual colaborador está sendo editado

  // Carregar dados do localStorage ao iniciar
  useEffect(() => {
    const dados = JSON.parse(localStorage.getItem("colaboradores")) || [];
    setColaboradores(dados);

    // Verifica se o consentimento LGPD foi dado
    const consentimento = localStorage.getItem("lgpd-consentido");
    if (consentimento !== "true") {
      setIsModalOpen(true); // Se não foi dado, abre o modal de consentimento
    }
  }, []);

  // Função para adicionar ou editar colaborador
  const salvarColaborador = () => {
    const salarioFormatado = parseFloat(salario.replace(/[^\d.-]/g, '')).toFixed(2); // Remover qualquer caractere não numérico e formatar o salário

    if (editingId) {
      // Editar colaborador
      const colaboradoresAtualizados = colaboradores.map((colaborador) =>
        colaborador.id === editingId
          ? { ...colaborador, nome, email, cargo, salario: salarioFormatado }
          : colaborador
      );
      setColaboradores(colaboradoresAtualizados);
    } else {
      // Adicionar novo colaborador
      const novoColaborador = { nome, email, cargo, salario: salarioFormatado, id: Date.now() };
      setColaboradores([...colaboradores, novoColaborador]);
    }

    localStorage.setItem("colaboradores", JSON.stringify(colaboradores));
    setIsAddingColaborador(false); // Fecha o formulário após salvar
    resetForm(); // Limpa os campos
  };

  // Função para excluir colaborador
  const excluirColaborador = (id) => {
    const colaboradoresAtualizados = colaboradores.filter((colaborador) => colaborador.id !== id);
    setColaboradores(colaboradoresAtualizados);
    localStorage.setItem("colaboradores", JSON.stringify(colaboradoresAtualizados));
  };

  // Função para reiniciar o localStorage
  const reiniciarLocalStorage = () => {
    localStorage.removeItem("colaboradores");
    setColaboradores([]);
  };

  // Função para limpar os campos do formulário
  const resetForm = () => {
    setNome('');
    setEmail('');
    setCargo('');
    setSalario('');
    setEditingId(null); // Limpa o ID de edição
  };

  // Função para preencher os campos de edição
  const editarColaborador = (colaborador) => {
    setNome(colaborador.nome);
    setEmail(colaborador.email);
    setCargo(colaborador.cargo);
    setSalario(colaborador.salario); // Preenche com o valor já formatado
    setEditingId(colaborador.id); // Salva o ID do colaborador que está sendo editado
    setIsAddingColaborador(true); // Abre o formulário de adicionar/editar
  };

  // Formatar o salário como moeda
  const formatarSalario = (salario) => {
    return parseFloat(salario).toLocaleString('pt-BR', {
      style: 'currency',
      currency: 'BRL',
    });
  };

  // Função para exportar dados para Excel
  const baixarPlanilha = () => {
    if (!colaboradores || colaboradores.length === 0) {
      alert('Não há colaboradores para exportar!');
      return;
    }

    // Remover o campo ID antes de exportar
    const colaboradoresSemId = colaboradores.map(({ id, ...rest }) => rest);

    // Criar uma planilha formatada
    const ws = XLSX.utils.json_to_sheet(colaboradoresSemId);
    const wb = XLSX.utils.book_new();
    
    // Definir título na planilha
    const titulo = { A1: { v: "Lista de Colaboradores", s: { font: { bold: true, size: 14 } } } };
    XLSX.utils.sheet_add_json(ws, colaboradoresSemId, { header: ["nome", "email", "cargo", "salario"], skipHeader: true });

    // Anexar título e dados na planilha
    XLSX.utils.book_append_sheet(wb, ws, 'Colaboradores');
    wb.Sheets['Colaboradores'] = { ...titulo, ...wb.Sheets['Colaboradores'] };
    
    // Gerar o arquivo Excel
    XLSX.writeFile(wb, 'colaboradores.xlsx');
  };

  // Função para salvar o consentimento LGPD no localStorage
  const aceitarLGPD = () => {
    localStorage.setItem("lgpd-consentido", "true");
    setIsModalOpen(false); // Fecha o modal após o consentimento
  };

  return (
    <div>
      {/* Modal de Consentimento LGPD */}
      {isModalOpen && (
        <ModalLGPD onAceitar={aceitarLGPD} />
      )}

      {/* Tela de Colaboradores */}
      {!isModalOpen && (
        <div className="container">
          <h1>Lista de Colaboradores</h1>

          {/* Botões */}
          <button className="add-button" onClick={baixarPlanilha}>Baixar Planilha Excel</button>
          <button className="add-button" onClick={() => setIsAddingColaborador(true)}>Adicionar Colaborador</button>
          <button className="add-button" onClick={reiniciarLocalStorage}>Reiniciar Local Storage</button>

          {/* Container para Adicionar ou Editar Colaborador */}
          {isAddingColaborador && (
            <div className="add-colaborador-container">
              <h2>{editingId ? 'Editar Colaborador' : 'Adicionar Novo Colaborador'}</h2>
              <input type="text" placeholder="Nome" value={nome} onChange={(e) => setNome(e.target.value)} />
              <input type="email" placeholder="Email" value={email} onChange={(e) => setEmail(e.target.value)} />
              <input type="text" placeholder="Cargo" value={cargo} onChange={(e) => setCargo(e.target.value)} />
              <input
                type="text"
                placeholder="Salário"
                value={salario}
                onChange={(e) => setSalario(e.target.value.replace(/[^\d,.-]/g, ''))} // Permitindo apenas números e vírgulas
              />
              <button onClick={() => { setIsAddingColaborador(false); resetForm(); }}>Cancelar</button>
              <button onClick={salvarColaborador}>Salvar</button>
            </div>
          )}

          {/* Tabela de Colaboradores */}
          <TabelaColaboradores
            colaboradores={colaboradores}
            onEditar={editarColaborador}
            onExcluir={excluirColaborador}
            formatarSalario={formatarSalario}
          />
        </div>
      )}
    </div>
  );
};

export default App;
